  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', Sans-Serif;
  background: #EEF0F4;
  min-height: 100vh;
  color: #fff;
  user-select: none;
}

header {
  width: 100%;
  display: flex;
  background: #fff;
  padding: 30px 25px;
  justify-content: space-between;
  z-index: 1000;
}

header h1 {
  color: #002765;
}

header nav button {
  padding: 8px 5px;
  border: 0;
  border-radius: 8px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.2);
  margin-left: 5px;
  background: #ff5945;
  color: #FDFDFD;
  font-family: 'Poppins', Sans-Serif;
  font-weight: bold;
  cursor: pointer;
}

header nav button:hover {
  background: #fff;
  color: #ff5945;
  border: 1px solid #ff5945;
}

.hero {
  padding: 25px 25px 20px;
  background: #002765;
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}

.hero h1 {
  color: #ff5945;
  font-size: 2rem;
}

.hero p {
  margin: 30px auto;
}

.hero button {
  padding: 10px 15px;
  border: 0;
  border-radius: 20px;
  background: #ff5945;
  color: #002765;
  font-size: 1rem;
  font-weight: bold;
  margin: 0;
}

.hero-image {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 350px;
  border-radius: 10px;
  position: relative;
  top: 60px;
  margin: 0 auto; 
}

/* Today's Special Section */
.todays-special {
  padding: 2rem;
  background-color: #f9f9f9;
}

.todays-special h2 {
  text-align: center;
  font-size: 2rem;
  margin-top: 3.5rem;
  margin-bottom: 2rem;
  color: #002765;
}

.specials {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1.5rem;
}

.special, .menu-item {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 300px;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.special:hover, .menu-item:hover {
  transform: translateY(-5px);
}

.special-img img, .menu-item-img img {
  width: 100%;
  height: auto;
  display: block;
}

.special-details, .menu-item-details {
  padding: 1rem;
  text-align: center;
}

.dish-name {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
  color: #333;
}

.price {
  font-size: 1.1rem;
  color: #e67e22;
  margin-left: 0.5rem;
}

.dish-description {
  font-size: 0.9rem;
  color: #777;
  margin-bottom: 1rem;
}

.order {
  font-size: 0.9rem;
  font-weight: bold;
  color: #2ecc71;
}

.delivery-icon {
  width: 20px;
  height: 20px;
  display: inline-block;
  background-size: contain;
  vertical-align: middle;
}

.opening-hours {
  background-color: #002765;
  padding: 40px;
  text-align: center;
}

.opening-hours h2 {
  margin-bottom: 10px;
  color: #ff5945;
}

.opening-hours p {
  margin: 5px 0;
  font-size: 16px;
}

.newsletter {
  padding: 40px;
  text-align: center;
  margin-top: 20px;
}

.newsletter h2 {
  margin-bottom: 10px;
  color: #002765;
}

.newsletter form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.newsletter input[type="email"] {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 250px;
  margin-right: 10px;
}

.newsletter button {
  padding: 10px 20px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.newsletter button:hover {
  background-color: #218838;
}

/* Menu Section */
.menu {
  padding: 2rem;
  background-color: #f9f9f9;
}

.menu h2 {
  text-align: center;
  font-size: 2rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  color: #002765;
}

.menu-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
}

.abt-hero {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: cover;
  background-color: rgb(51, 51, 51); 
  text-align: center;
}

.abt-hero .overlay {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 100px 25px;
}

.about-content {
  padding: 25px;
}

.about-content .about-text p {
  color: #333;
  line-height: 1.5rem;
  margin-bottom: 1rem;
  text-align: justify;
}

.footer {
  background-color: #002765;
  color: white;
  padding: 20px;
  text-align: left;
}

.footer-content {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
  flex-direction: column;
  gap: 40px;
}

.footer-section h2,
.footer-section h3 {
  margin-bottom: 10px;
}

.footer-section h2 {
  color: #ff5945;
  font-size: 2rem;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin-bottom: 5px;
}

.footer-section ul li a {
  color: white;
  text-decoration: none;
}

.footer-section ul li a:hover {
  text-decoration: underline;
}

.footer-bottom {
  border-top: 1px solid #555;
  padding-top: 10px;
  text-align: center;
}

@media only screen and (min-width: 1024px) {
  .hero {
    flex-direction: row;
    justify-content: space-between;
    padding: 35px;
    gap: 40px;
  }
  
  .hero-image {
    max-width: 700px;
  }
  
  .footer-content {
    flex-direction: row;
  } 
}


